
@import url("https://fonts.googleapis.com/css2?family=Cormorant+Garamond:ital,wght@1,300&display=swap");

.repair {
  width: 40%;
  position: absolute;
  right: 15%;
  top: 15%;
  opacity: 0.1;
  pointer-events: none;
}

.parinteContainer {
    display: grid;
    width: 1500px;
    grid-template-columns: 600px 900px;
}

.bigContainer {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  height: 80vh;
  position: relative;
}

.container1 {
  height: 50%;
}
.container1 h1 {
  position: absolute;
  width: 250px;
  text-align: center;
  top: 10%;
  left: 4%;
}
.container2 h1 {
  position: absolute;
  text-align: center;
  width: 250px;
  top: 50%;
  right: 0;
}
.copil {
  width: 40%;
  position: absolute;
  right: 0;
  top: 3%;
}

.adult {
  width: 50%;
  position: absolute;
  left: 5%;
  top: 40%;
}

.loveH1 {
  position: absolute;
  bottom: 5%;
  width: 60%;
  text-align: center;
  left: 10%;
}

.textH1 {
  color: #30bd92;
  text-transform: uppercase;
  font-family: 'Cormorant Garamond', serif;
  letter-spacing: 1px;
}

.textContainer {
    position: absolute;
    left: 35%;
    width: 70vw;
    height: 100vh;
    text-align: center;
    top: 20%;
    color: #24858b;
    letter-spacing: 1px;
    font-family: 'Cormorant Garamond', serif;
    background: rgba(46, 139, 86, 0.075);
    border-radius: 5px;
}
.textContainer h1 {
    padding-top: 2rem;
    padding-bottom: 4rem;
    text-transform: uppercase;
    color: #30bd92;
}

.textContainer h1::after {
    content: '';
    position: absolute;
    border-top: 3px solid #30bd92;
    width: 400px;
    left: 35%;
}

.textContainer h3 {
    padding: 1rem;
    text-transform: uppercase;
}
.textContainer h3:nth-child(7) {
    color: #30bd92;
}
.textContainer h2 {
    padding-top: 3rem;
}

