@import url("https://fonts.googleapis.com/css2?family=Cormorant+Garamond:ital,wght@1,300&display=swap");


.cool {
  position: absolute;
  right: 10%;
  width: 60%;
  animation: animate 60s linear infinite;
  transition: all 60s ease-in-out;
}
.cool1 {
  position: absolute;
  z-index: -22;
  bottom: 0;
  right: 10%;
  width: 40%;
  animation: animate 40s linear infinite;
  transition: all 60s ease-in-out;
}
.cool2 {
  position: absolute;
  z-index: -32;
  bottom: 30%;
  right: 3%;
  width: 40%;
  animation: animate 20s linear infinite;
  transition: all 60s ease-in-out;
}
.imageBack {
  position: absolute;
  right: -5%;
  top: -5%;
  opacity: 0.4;
  z-index: -1;
  border-radius: 50%;
  width: 1200px;
  height: 1200px;
  background: rgba(167, 121, 180, 0.486);
}

.designClip {
  clip-path: polygon(0 36%, 11% 78%, 100% 100%, 0% 100%);
  position: absolute;
  background-color: rgba(19, 231, 196, 0.616);
  width: 100vw;
  height: 30vh;
  bottom: 0%;
  opacity: 6;
  z-index: -98;
}
.designClip2 {
  clip-path: polygon(0 0, 14% 61%, 100% 100%, 0% 100%);
  position: absolute;
  background-color: rgba(19, 231, 203, 0.534);
  width: 100vw;
  height: 30vh;
  bottom: 0%;
  opacity: 6;
  z-index: -99;
}

.prezentare {
  position: absolute;
  width: 700px;
  padding: 3rem;
  text-align: center;
  left: 3rem;
  font-family: 'Cormorant Garamond', serif;
  font-size: 17px;
}
.prezentare h1 {
  padding-bottom: 2rem;
  color: rgb(18, 122, 79);
}

.prezentare2 {
  position: absolute;
  left: 3rem;
  bottom: 23%;
  width: 700px;
  padding: 3rem;
  text-align: center;
  font-family: 'Cormorant Garamond', serif;
  font-size: 20px;
}
.prezentare2 h2 {
  padding-bottom: 2rem;
  color: rgb(18, 122, 79);
}
.linieIntre {
  position: absolute;
  left: 5%;
  bottom: 52%;
  width: 600px;
  border: 3px solid rgb(43, 226, 150);
}

.citat {
  position: absolute;
  width: 600px;
  top: 25%;
  right: 12%;
  font-size: 3rem;
}
.citat h3 {
  color: rgb(16, 112, 124);
  font-family: 'Cormorant Garamond', serif;
}

@keyframes animate {
  0% {
    transform: translateX(1000px);
    opacity: 0.2;
  }
  40% {
    opacity: 0.4;
  }
  60% {
    opacity: 0.5;
  }
  80% {
    opacity: 0.3;
  }
  100% {
    opacity: 0;
  }
}

/* animation */
