@import url("https://fonts.googleapis.com/css2?family=Nanum+Myeongjo&display=swap");

.forma {
  text-align: center;
  padding: 9rem;
}
.forma h1 {
  padding-bottom: 3rem;
  color: rgb(31, 103, 167);
  margin-top: 40px;
  text-align: center;
  width: 500px;
  margin: auto;
  font-family: "Nanum Myeongjo", serif;
}
.forma input {
  padding: 1rem;
  margin: 1rem;
  border-radius: 10px;
  border: 2px solid purple;
}
.forma button {
  padding: 20px;
  margin: 20px 0;
  border-radius: 10px;
  border: none;
  background: #33001b;
  color: white;
  transition: all 0.3s ease-in-out;
}
.forma button:hover {
  transform: scale(1.2);
  transition: all 0.3s ease-in-out;
  background: blueviolet;
}
.anunt {
  font-size: 15px;
  color: #726e6e;
}

.clip {
  position: absolute;
  width: 2500px;
  height: 2500px;
  left: -15%;
  top: -60%;
  border-radius: 50%;
  background: rgba(11, 198, 223, 0.151);
  z-index: -9999;
}

.bike12 {
  width: 50%;
  left: 25%;
  top: 10px;
  opacity: 0.4;
  /* z-index: -999; */
  position: absolute;
  animation: animate 40s linear infinite;
  pointer-events: none;
}
.bike11 {
  width: 40%;
  position: absolute;
  opacity: 0.4;
  animation: animate 40s 1s linear infinite reverse ;
  right: -260px;
  top: -5px;
  /* z-index: -999; */
  pointer-events: none;
}
.bike10 {
  width: 50%;
position: absolute;
bottom: -580px;
opacity: .4;
left: -250px;
animation: animate 40s linear infinite reverse;
pointer-events: none;

}

.alternativ {
  position: relative;
  width: 400px;
  height: 700px;
  background: #81d3c536;
  margin-top: -470px;
  margin-left: -110px;
  box-shadow: rgba(63, 134, 128, 0.178) 2px 2px 2px 1px;
  border-radius: 0 20px 0 0;
}

.imagine1 , .imagine2 , .imagine3 , .imagine4 {
  width: 40px;
}

.locationText {
  position: absolute;
  left: 6rem;
  top: 10rem;
}

.phoneText {
  position: absolute;
  left: 6rem;
  top: 18rem;
}
.imagine3 {
  position: absolute;
  top: 25rem;
  left: 9rem;
  transition: all .4s;
}
.imagine3:hover {
  transform: scale(1.1);
}
.imagine4 {
  position: absolute;
  top: 25rem;
  left: 14rem;
  transition: all .4s;
}
.imagine4:hover {
  transform: scale(1.1);
}
.subtextMeniu {
  position: absolute;
  top: 3rem;
  left: -3rem;
}

.eticheta {
  width: 110px;
  height: 50px;
  background: rgb(20, 220, 160);
  position: absolute;
  top: 10.5rem;
  left: 0;
  border-radius: 5px;
  transition: all .4s;
  transform: rotate(270deg);
}
.eticheta:hover {
  transform: scale(1.1);
  background: rgb(20, 197, 220);
}

.eticheta h2 {
  line-height: 50px;
  color: rgb(12, 85, 61);
}
.eticheta h2:hover {
  color: rgb(100, 95, 95);
}
.hideCheck {
  display: none;
}

.alternativ , .hideCheck {
  transform: translateX(-500px);
  transition: all .8s ;
}

input:checked + div {
  opacity: 1;
  transform: translateX(0);

}

.design {
  position: absolute;
  width: 100%;
  height: 60px;
  bottom: 0;
  background: rgba(20, 220, 160, 0.342);
}
.design h3 {
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgb(71, 69, 201);
  padding-right: 10px;
}
.imagineDesign {
  width: 30px;
}
.design h3:hover {
  color: #058f5a;
}

