@import url("https://fonts.googleapis.com/css2?family=Nanum+Myeongjo&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Cormorant+Garamond:ital,wght@1,300&display=swap");

* {
  margin: 0%;
  padding: 0;
  box-sizing: border-box;
  overflow: hidden;
}

.mainImage {
  position: absolute;
  width: 40%;
  height: auto;
  top: 160px;
  right: 10%;
}



.cool {
  position: absolute;
  bottom: 0;
  width: 40%;
  left: 0;
  pointer-events: none;
}

.titluContainer {
    position: absolute;
    bottom: -400px;
    right: -250px;
    z-index: -1;
    width: 800px;
    height: 800px;
    background: rgb(139, 5, 133);
    opacity: .3;
    border-radius: 50%;
}
.titluContainer1 {
  position: absolute;
    top: -250px;
    left: -300px;
    z-index: -1;
    width: 800px;
    height: 800px;
    background: rgba(139, 5, 132, 0.63);
    opacity: .3;
    border-radius: 50%;
}
.titlu {
    font-family: 'Nanum Myeongjo', serif;
    width: 800px;
    color: rgb(8, 189, 128);
    position: absolute;
    top: 22%;
    left: 8%;
    font-size: 4rem;
    opacity: 1;
}
.postscriptum {
    position: absolute;
    bottom: 3%;
    right: 5%;
    color: darkblue;
}

.citatPrimaPagina {
  position: absolute;
  width: 600px;
  bottom: 8%;
  text-align: center;
  left: 40%;
}
.citatPrimaPagina h3 {
  font-size: 29px;
  font-family: 'Cormorant Garamond', serif;
  color: #30bd92;
}